import * as PIXI from "pixi.js-legacy";
import gsap from "gsap";
import Util from "../logic/Util";

export default class Pickup extends PIXI.Container {
    constructor(coords, boardSize, cellSize) {
        super();
        this.x = 16;
        this.y = 16;
        this.coords = coords;
        this.boardSize = boardSize;
        this.cellSize = cellSize;

        this.g = this.draw();
        this.addChild(this.g);
        // this.pivot = new PIXI.Point(0.5, 0.5);
        // this.anchor = new PIXI.Point(0.5, 0.5);
        // this.pivot.x = this.width / 2;
        // this.pivot.y = this.height / 2;
        // this.g.anchor = new PIXI.Point(0.5, 0.5);
        // gsap.to(this, {
        //     duration: 2,
        //     repeat: -1,
        //     yoyo: true,
        //     rotation: Math.PI
        // });
    }
    draw() {
        let c = Util.localToWorld(this.coords);
        const g = new PIXI.Graphics();
        const size = this.cellSize;
        g.beginFill(0xeeee44);
        g.lineStyle(2);
        g.drawStar(c[0] * size, c[1] * size, 6, 6, 14);
        g.closePath();
        g.endFill();
        return g;
    }
}
